.battle {
  padding: 0 0.32rem;

  .battle-title {
    margin-top: 0.1rem;
    margin-bottom: 0.1rem;
    width: 100%;
    text-align: center;
    font-weight: bold;
  }

  .battle-step {
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -moz-box-pack: center;
     -ms-flex-pack: center;
         justify-content: center;
    width: 100%;

    .battle-step-item {
      display: -moz-box;
      display: -ms-flexbox;
      display: flex;
      -moz-box-orient: vertical;
      -moz-box-direction: normal;
       -ms-flex-direction: column;
           flex-direction: column;
      -moz-box-align: center;
       -ms-flex-align: center;
           align-items: center;

      .step-item-name {
        font-size: 0.2rem;
        margin: 0.08rem 0.16rem;
      }

      .step-item-icon {
        font-size: 0.6rem;
        width: 100%;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        -moz-box-align: center;
         -ms-flex-align: center;
             align-items: center;
        -moz-box-pack: center;
         -ms-flex-pack: center;
             justify-content: center;
        background-color: #eeeeee;
        padding: 0.16rem;
      }

      .step-item-icon-user {
        @extend .step-item-icon;
        color: #f5c27f;
      }

      .step-item-icon-battle {
        @extend .step-item-icon;
        color: red;
      }

      .step-item-icon-winner {
        @extend .step-item-icon;
        color: #f2ea57;
      }
    }
  }

  .battle-player {
    margin-top: 0.1rem;

    .battle-player-title {
      width: 100%;
      text-align: center;
      font-weight: bold;
    }

    .battle-player-context {
      display: -moz-box;
      display: -ms-flexbox;
      display: flex;
      -moz-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;

      .battle-player-context-item {
        margin-top: 0.1rem;
        width: 48%;

        .context-item-title {
          font-weight: bold;
          margin-bottom: 0.1rem;
        }

        .context-item-data {
          display: -moz-box;
          display: -ms-flexbox;
          display: flex;
          -moz-box-align: center;
           -ms-flex-align: center;
               align-items: center;

          .ant-btn {
            height: auto;
          }

          .context-item-data-input {
            margin-right: 0.1rem;
          }
        }
      }

      .render-player {
        margin-top: 0.1rem;
        width: 48%;
        padding: 0.16rem;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        -moz-box-align: center;
         -ms-flex-align: center;
             align-items: center;
        -moz-box-pack: justify;
         -ms-flex-pack: justify;
             justify-content: space-between;
        background-color: #dfdfdf;
        border-radius: 0.04rem;

        .render-player-data {
          display: -moz-box;
          display: -ms-flexbox;
          display: flex;
          -moz-box-align: center;
           -ms-flex-align: center;
               align-items: center;

          img {
            border-radius: 0.08rem;
            width: 0.36rem;
            height: 0.36rem;
            margin-right: 0.08rem;
          }
          item-image{
            border-radius: 0.08rem;
            width: 0.36rem;
            height: 0.36rem;
            margin-right: 0.08rem;
          }
          .player-name {
            font-size: 0.2rem;
            font-weight: bold;
            color: #1890ff;
          }
        }

        .render-player-icon {
          font-size: 0.18rem;
          color: red;
          cursor: pointer;
        }
      }
    }

    .battle-button {
      margin-top: 0.1rem;
      display: -moz-box;
      display: -ms-flexbox;
      display: flex;
      -moz-box-align: center;
       -ms-flex-align: center;
           align-items: center;
      -moz-box-pack: center;
       -ms-flex-pack: center;
           justify-content: center;

      .ant-btn {
        height: auto;
      }
    }
  }
}